import uniPopup from '@/components/uni-popup/uni-popup.vue'
import {
	mapGetters
} from 'vuex'
export default {
	props: {
		isPass: {
			type: Boolean,
			required: true
		},
		isPaddingBottom: {
			type: Boolean,
			default: false
		}
	},
	components: {
		uniPopup
	},
	computed: {
		...mapGetters(['user'])
	},
	data() {
		return {
			auditIdea: ''
		}
	},
	mounted() {
		this.initAuditIdea()
	},
	watch: {
		isPass() {
			this.initAuditIdea()
		}
	},
	methods: {
		// 初始化审核意见
		initAuditIdea() {
			this.auditIdea = this.isPass ? '通过' : '不通过'
		},
		cancelClick() {
			this.$refs.share.close()
			// 更新数据
			this.$emit('updateQuery')
		},
		passClick() {
			uni.showLoading({
				title: '正在提交数据...'
			})
			/**
			 * todo: api请求审批意见
			 */
			setTimeout(() => {
				uni.hideLoading()
				uni.showToast({
					title: '审批通过！',
					icon: 'success'
				})
				// 取消返回更新
				this.cancelClick()
			}, 1000)
		},
		unPassClick() {
			uni.showLoading({
				title: '正在提交数据...'
			})
			/**
			 * todo: api请求审批意见
			 */
			setTimeout(() => {
				uni.hideLoading()
				uni.showToast({
					title: '审批不通过！',
					icon: 'none'
				})
				// 取消返回更新
				this.cancelClick()
			}, 1000)
		}
	}
}
